import { detailMovieOrTvTemplate } from '../../templates/templateCreator';
import MoviesDB from '../data/MoviesDB';
import dataRender from '../utility/dataRender';

const Tv = {
  render() {
    return `
        <div class="container-content">
            <slider-tag id="trending-tv"></slider-tag>
            <list-tag id="on-air"></list-tag>
            <div class="latest mt-5 bg-slate-800">
              <div class="text-slate-50 py-2 px-4">
                <h2 class="font-bold text-lg">Latest Tv Show</h2>
              </div>
            </div>
            <list-tag id="top-rated"></list-tag>
            <list-tag id="popular-tv"></list-tag>
        </div>
        <aside>
          <aside-tag id="trending-movies"></aside-tag>
        </aside>
    `;
  },

  async afterRender() {
    const tvSliderContainer = document.querySelector('#trending-tv');
    const tvOnAirContainer = document.querySelector('#on-air');
    const tvLatestContainer = document.querySelector('.latest');
    const topRatedContainer = document.querySelector('#top-rated');
    const popularTvContainer = document.querySelector('#popular-tv');
    const asideTrendingContainer = document.querySelector('#trending-movies');

    new dataRender({
      data: MoviesDB.Trending(1, 'tv'),
      container: tvSliderContainer,
    }).render();

    new dataRender({
      title: 'On Air Tv Shows',
      data: MoviesDB.TvOnAir(),
      container: tvOnAirContainer,
      totalData: 10,
      link: true,
      slug: 'on-air-tv',
    }).render();

    new dataRender({
      title: 'Top Rated Tv Shows',
      data: MoviesDB.TopRated(1, 'tv'),
      container: topRatedContainer,
      totalData: 10,
      link: true,
      slug: 'top-rated-tv',
    }).render();

    new dataRender({
      title: 'Popular Tv Shows',
      data: MoviesDB.PopularMovieOrTv(1, 'tv'),
      container: popularTvContainer,
      totalData: 10,
      link: true,
      slug: 'popular-tv',
    }).render();

    new dataRender({
      title: 'Trending Movies',
      data: MoviesDB.Trending(),
      container: asideTrendingContainer,
    }).render();

    const data = await MoviesDB.Latest('tv');
    const cast = await MoviesDB.CreditMovieOrTv(data.id, 'tv');
    tvLatestContainer.innerHTML += detailMovieOrTvTemplate(data, cast, '');
  },
};

export default Tv;
