const urlParse = {
  parseUrl() {
    const url = window.location.hash.slice(1);
    const splitedUrl = this.splitUrl(url);
    return this.urlCombiner(splitedUrl);
  },

  parseUrlWithoutCombiner() {
    const url = window.location.hash.slice(1).toLowerCase();
    return this.splitUrl(url);
  },

  splitUrl(url) {
    const split = url.split('/');
    return {
      pages: split[1] || null,
      id: split[2] || null,
      season: split[3] || null,
      idSeason: split[4] || null,
    };
  },

  urlCombiner(splitedUrl) {
    return (splitedUrl.pages ? `/${splitedUrl.pages}` : '/')
      + (splitedUrl.id ? '/:id' : '')
      + (splitedUrl.season ? `/${splitedUrl.season}` : '')
      + (splitedUrl.idSeason ? '/:idSeason' : '');
  },
};

export default urlParse;
